import {
  arrow__left,
  arrow__right,
  bar,
  calendar__image,
  calendar_timing,
  calendar,
  chart__bar,
  chart_donught,
  slider1,
  time_clock_circle,
  calendar__bttom,
  header__image__mobile,
  users,
  sheep,
  clandestins,
  car,
} from "src/assets/images";

import Gallery from "../shared/Gallery";
import { bottomSLider } from "../shared/Slider/dataSlider";
import Slider2 from "../shared/Slider/slider2";
import { i18n } from "src/i18n";
import Image from "../shared/Image";
import { useEffect } from "react";
// publications
import selectorsCategory from "src/modules/categoryPublication/list/categoryPublicationListSelectors";
import actionsPublication from "src/modules/publication/list/publicationListActions";
import selectorsPublication from "src/modules/publication/list/publicationListSelectors";
import actionsCategory from "src/modules/categoryPublication/list/categoryPublicationListActions";
import { Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

// archives
import Archives from "src/view/Archive/Archives";
import { baseUrl } from "src/view/shared/BaseUrl";
import moment from "moment";
import Translate from "src/view/shared/Translate";
function Home() {
  const dispatch = useDispatch();
  const langue = localStorage.getItem("language");

  useEffect(() => {
    dispatch(actionsPublication.allpublicationbythematique());
    dispatch(actionsPublication.allpublicationbyCategory());
    dispatch(actionsPublication.doFetchValidePublication());
    dispatch(actionsCategory.doFetch());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const rowsPublicationByThematique = useSelector(
    selectorsPublication.selectPublicationByThematiqueRows
  );
  const rowsPublication = useSelector(selectorsPublication.selectRows);
  const selectRowsCategory = useSelector(selectorsCategory.selectRows);
  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectRowsCategory]);
  const limitPubs =
    selectRowsCategory[0].publications.length < 4
      ? selectRowsCategory[0].publications.length
      : 4;

  const limitPubsListe =
    selectRowsCategory[1].publications.length < 6
      ? selectRowsCategory[1].publications.length
      : 6;
  const sliderTop = (obj) => {
    return (
      <div className="content__sliderTop">
        <h3>{langue === "ar" ? obj?.titleAR : obj?.titleFR}</h3>
      </div>
    );
  };

  const sliderBottom = (obj, index, slideIndex) => {
    return (
      <div className={slideIndex === index + 1 ? "active__slideBottom" : ""}>
        <Image
          className="lazyload"
          src={obj?.photos?.length > 0 ? obj?.photos[0].downloadUrl : slider1}
          alt=" Slider Icon"
        />
        <p
          className={
            slideIndex === index + 1 ? "text__p text__gras" : "text__p"
          }
        >
          {langue === "ar" ? obj?.titleAR : obj?.titleFR}
        </p>
      </div>
    );
  };

  const renderPublications = (i) => {
    return (
      <div>
        <Image
          className="lazyload"
          src={
            selectRowsCategory[0].publications[i + 1].photos.length > 0
              ? `${baseUrl}/file/download?privateUrl=${
                  selectRowsCategory[0].publications[i + 1].photos[0].privateUrl
                }`
              : `https://placehold.jp/670x354.png`
          }
          alt="publication2 Icon"
        />
        <div className="list__detaill">
          <div className="detaill__header">
            <div className="header__left">
              <p>
                {langue === "fr"
                  ? selectRowsCategory[0].publications[i + 1].titleFR
                  : selectRowsCategory[0].publications[i + 1].titleAR}
              </p>
            </div>
            <div className="header__right">
              {moment(selectRowsCategory[0].publications[i + 1].date).format(
                "L"
              )}
            </div>
          </div>
          <div
            className="detaill__content"
            dangerouslySetInnerHTML={{
              __html: Translate.Trans(
                "description",
                selectRowsCategory[0].publications[i + 1]
              ),
            }}
          ></div>
        </div>
      </div>
    );
  };
  return (
    <div className="app">
      {/* Styling the Header */}

      <div className="app__calendar">
        <img src={calendar__bttom} alt="" />
      </div>
      <div className="app__header">
        <div className="header__content" style={{ height: 549 }}>
          <div className="content">
            <div className="content__imagePrincipale">
              <Slider2
                rows={rowsPublication}
                height={549}
                width={1170}
                label="slider"
                render={sliderTop}
                showDots={false}
                bottomrender={sliderBottom}
              />

              <img
                className="lazyload"
                src={header__image__mobile}
                id="image__mobile"
                alt="Header "
              />
            </div>
            {/* Slider Top */}

            {/* Slider Bottom  */}
          </div>
        </div>
      </div>

      {/* Styling the statestique */}
      <div className="app__statestique">
        <div className="statestique">
          <div className="archieve__header">
            <h2>tableau&nbsp;de&nbsp;bord</h2>
            <div className="satestique__bar" />
          </div>
          <div className="statestique__graph">
            <div className="graph__left">
              <div className="graph__title">
                <div className="title">
                  Statistiques migration premier semestre 2020
                </div>
                <div className="graph__navigate">
                  <div className="navigate__left">
                    <i className="fa-solid fa-chevron-left" />
                  </div>
                  <div className="navigate__right">
                    <i className="fa-solid fa-chevron-right" />
                  </div>
                </div>
              </div>
              <div className="graph__description">
                <div className="descripition__left">
                  <div>
                    <div className="description__number">
                      <h2>236</h2>
                    </div>
                    <div className="description__text">
                      Tentatives échouées de départ clandestins
                    </div>
                  </div>
                  <div>
                    <div className="description__number">
                      {" "}
                      <h2> 34</h2>
                    </div>
                    Tentatives échouées de départ clandestins
                  </div>
                  <div>
                    <div className="description__number">
                      {" "}
                      <h2> 67</h2>
                    </div>
                    Tentatives échouées de départ clandestins
                  </div>
                </div>
                <div className="description__right">
                  <img
                    className="lazyload"
                    src={chart_donught}
                    alt="chart_donught Image"
                  />
                </div>
              </div>
              <div className="global__button">Plus de détails</div>
            </div>
            <div className="graph__right">
              <div className="graph__title">
                <div className="title">Statistiques migration 2018</div>
                <div className="graph__navigate">
                  <div className="navigate__left">
                    <i className="fa-solid fa-chevron-left" />
                  </div>
                  <div className="navigate__right">
                    <i className="fa-solid fa-chevron-right" />
                  </div>
                </div>
              </div>
              <div className="detaill__graph">
                Malgré leur provenance et origines différentes, leur
                revendication est la même : l’évacuation de la Tunisie et la
                réinstallation dans un pays où les droits humains sont
                respectés...
              </div>
              <img className="lazyload" src={chart__bar} alt="" />
              <div className="detaill__graph">
                Malgré leur provenance et origines différentes
              </div>
              <div className="global__button">Plus de détails</div>
            </div>
          </div>
          <div className="statesitique__global">
            <div className="global__left">
              <div className="global__header">
                Statistiques migration premier semestre 2020
              </div>
              <div className="global__content">
                Malgré leur provenance et origines différentes, leur
                revendication est la même : l’évacuation de la Tunisie et la
                réinstallation dans un pays où les droits humains sont
                respectés... <br />
                Malgré leur provenance et origites, leur revendication est la
                même : l’évacuation de la Tunisie et la réinstallation dans un
                pays où les droits humains sont respectés...
              </div>
              <div className="global__button">Plus de détails</div>
            </div>
            <div className="global__right">
              <div>
                <div className="global__number">
                  236
                  <img src={users} alt="" />
                </div>
                <div className="detaill__number">
                  Tentatives échouées de départ clandestins
                </div>
              </div>
              <div>
                <div className="global__number">
                  1564
                  <img src={sheep} alt="" />
                </div>
                <div className="detaill__number">
                  Clandestins arrivés en Italie
                </div>
              </div>
              <div>
                <div className="global__number">
                  85
                  <img src={clandestins} alt="" />
                </div>
                <div className="detaill__number">Clandestins expulsés</div>
              </div>
              <div>
                <div className="global__number">
                  7<img src={car} alt="" />
                </div>
                <div className="detaill__number">
                  Tentatives échouées de départ clandestins
                </div>
              </div>
            </div>
            <div className="global__navigations">
              <div className="arrow__left">
                <img
                  className="lazyload"
                  src={arrow__right}
                  alt="arrow__right ICon"
                />
              </div>
              <div className="arrow__right">
                <img
                  className="lazyload"
                  src={arrow__left}
                  alt="arrow__left Icon"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Styling the archeive */}
      <div className="archeive">
        <div className="archieve__header">
          <h2>{i18n("common.Archives")}</h2>
          <div className="header__bar" />
        </div>
        <div style={{ marginTop: 43 }}>
          <Archives acceuil={true} />
        </div>
      </div>
      {/* Stying the Publication */}
      {selectRowsCategory.length > 0 ? (
        <div className="app__publications">
          <div className="publication">
            {selectRowsCategory[0] ? (
              <div className="publication__left">
                <div className="archieve__header">
                  <h2>
                    {langue === "fr"
                      ? selectRowsCategory[0].titleFR
                      : selectRowsCategory[0].titleAR}
                  </h2>
                  <div className="publication__bar" />
                </div>
                {selectRowsCategory[0].publications.length > 0 ? (
                  <div className="publication__images">
                    <div className="images__header">
                      <div className="__header">
                        <Image
                          className="lazyload"
                          src={
                            selectRowsCategory[0].publications[0].photos
                              .length > 0
                              ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[0].publications[0].photos[0].privateUrl}`
                              : `https://placehold.jp/670x354.png`
                          }
                          alt="Publication Image"
                        />
                        <div className="__detaills">
                          <div className="list__detaill">
                            <div className="detaill__header">
                              <div className="__left">
                                <p>
                                  {langue === "fr"
                                    ? selectRowsCategory[0].publications[0]
                                        .titleFR
                                    : selectRowsCategory[0].publications[0]
                                        .titleAR}
                                </p>
                              </div>
                              <div className="__right">
                                {moment(
                                  selectRowsCategory[0].publications[0].date
                                ).format("L")}
                              </div>
                            </div>
                            <div
                              className="__content"
                              dangerouslySetInnerHTML={{
                                __html: Translate.Trans(
                                  "description",
                                  selectRowsCategory[0].publications[0]
                                ),
                              }}
                            ></div>
                          </div>
                        </div>
                      </div>
                    </div>
                    {selectRowsCategory[0].publications.length > 0 ? (
                      <div className="images__bottom">
                        {Array.from({ length: limitPubs - 1 }).map(
                          (item, index) => renderPublications(index)
                        )}
                      </div>
                    ) : null}
                  </div>
                ) : null}

                <div className="plus__button">{i18n("common.voirPlus")}</div>
              </div>
            ) : null}
            {selectRowsCategory.length > 0 ? (
              <div className="publication__right">
                <div className="communique">
                  <div className="archieve__header">
                    <h2>
                      {langue === "fr"
                        ? selectRowsCategory[1].titleFR
                        : selectRowsCategory[1].titleAR}
                    </h2>
                    <div className="communiquer__bar" />
                  </div>
                  {selectRowsCategory[1].publications.length > 0 ? (
                    <>
                      {Array.from({
                        length:
                          selectRowsCategory[1].publications.length <= 2
                            ? selectRowsCategory[1].publications.length
                            : 2,
                      }).map((item, i) => (
                        <div className="images__header">
                          <div className="__header">
                            <Image
                              className="lazyload"
                              src={
                                selectRowsCategory[1].publications[i].photos
                                  .length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[1].publications[i].photos[0].privateUrl}`
                                  : `https://placehold.jp/426x248.png`
                              }
                              alt="Publication Image"
                            />
                            <div className="__detaills">
                              <div className="list__detaill">
                                <div className="detaill__header">
                                  <div className="__left">
                                    <p>
                                      {langue === "fr"
                                        ? selectRowsCategory[1].publications[i]
                                            .titleFR
                                        : selectRowsCategory[1].publications[i]
                                            .titleAR}
                                    </p>
                                  </div>
                                  <div className="__right">
                                    {" "}
                                    {moment(
                                      selectRowsCategory[1].publications[i].date
                                    ).format("L")}
                                  </div>
                                </div>
                                <div
                                  className="__content"
                                  dangerouslySetInnerHTML={{
                                    __html: Translate.Trans(
                                      "description",
                                      selectRowsCategory[1].publications[i]
                                    ),
                                  }}
                                ></div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))}
                      <div className="communique">
                        <div className="">
                          <div className="publication__list">
                            {Array.from({ length: limitPubsListe - 2 }).map(
                              (item, i) => (
                                <div>
                                  <Image
                                    className="lazyload"
                                    src={
                                      selectRowsCategory[1].publications[i + 2]
                                        .photos.length > 0
                                        ? `${baseUrl}/file/download?privateUrl=${
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].photos[0].privateUrl
                                          }`
                                        : `https://placehold.jp/120x80.png`
                                    }
                                    alt=""
                                    id="publication__leftImage"
                                  />
                                  <div className="list__detaill">
                                    <div className="detaill__header">
                                      <div className="header__left">
                                        <p>
                                          {langue === "fr"
                                            ? selectRowsCategory[1]
                                                .publications[i + 2].titleFR
                                            : selectRowsCategory[1]
                                                .publications[i + 2].titleAR}
                                        </p>
                                      </div>
                                      <div className="header__right">
                                        {moment(
                                          selectRowsCategory[1].publications[
                                            i + 2
                                          ].date
                                        ).format("L")}
                                      </div>
                                    </div>
                                    <div
                                      className="detaill__content"
                                      dangerouslySetInnerHTML={{
                                        __html: Translate.Trans(
                                          "description",
                                          selectRowsCategory[1].publications[
                                            i + 2
                                          ]
                                        ),
                                      }}
                                    ></div>
                                  </div>
                                </div>
                              )
                            )}
                          </div>
                        </div>
                        <div className="plus__button">
                          {i18n("common.voirPlus")}
                        </div>
                      </div>
                    </>
                  ) : null}
                </div>
              </div>
            ) : null}
          </div>
        </div>
      ) : null}
      {/* Glossaire */}

      <div className="Glossaire">
        <h5>Glossaire</h5>
        <div className="glossaire__detaill">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla cursus
          et augue id consequat. Suspendisse sodales est lectus...
        </div>
        <div className="global__button">Découvrir</div>
      </div>

      {/* Styling the Evenement */}
      <div className="app__evenement">
        <div className="evenenement">
          <div className="archieve__header">
            <h2>Evènement</h2>
            <div className="satestique__bar" />
          </div>
          <div className="evenement__content">
            <div className="evenement__timing">
              <div className="timing__bar">
                <img className="lazyload" src={bar} alt="" />
              </div>
              <div className="timing__number">
                <div>
                  <div className="cercle__red" />
                  <div className="time">
                    <div className="date">17</div>
                    <div className="month">Avril</div>
                  </div>
                </div>
                <div>
                  <div className="cercle__red" />
                  <div className="time">
                    <div className="date">23</div>
                    <div className="month">Avril</div>
                  </div>
                </div>
                <div>
                  <div className="cercle__red" />
                  <div className="time">
                    <div className="date">7</div>
                    <div className="month">Mai</div>
                  </div>
                </div>
              </div>
            </div>
            <div className="evenement__description">
              {Array.from({ length: 3 }).map((item) => (
                <div>
                  <div className="evenement__left">
                    <Image
                      className="lazyload"
                      src={`https://placehold.jp/204x175.png`}
                      alt=""
                    />
                  </div>
                  <div className="evenement__right">
                    <div className="right__date">
                      <div>
                        <img
                          className="lazyload"
                          src={calendar}
                          alt=""
                          srcSet=""
                        />
                        17 avril 2022
                      </div>
                      <div>
                        <img
                          className="lazyload"
                          src={time_clock_circle}
                          alt=""
                        />
                        10h00
                      </div>
                    </div>
                    <div className="righ__desctiption">
                      Atelier de restitution de l’événement Echange des Jeunes
                      du projet Justice Environnementale
                    </div>
                    <div className="right__footer">
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                      Nulla cursus et augue id consequat. Suspendisse sodales
                      est lectus...
                    </div>
                  </div>
                </div>
              ))}
            </div>
            <div className="evenement__calendar">
              <div className="calendar">
                <img
                  className="lazyload"
                  src={calendar_timing}
                  alt=""
                  srcSet=""
                />
              </div>
              <div className="clendar__footer">
                <img
                  className="lazyload"
                  src={calendar__image}
                  alt=""
                  srcSet=""
                />
                <div className="calendar__month">
                  <div className="time" />
                  <div className="date">1</div>
                  <div className="month">Avril</div>
                </div>
                <div className="calendar__detaill">
                  Quel modèle de développement alternatif, durable , équitable
                  et démocratique
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* styling the gallery */}
      <Gallery />
    </div>
  );
}

export default Home;
